import { Metadata } from 'next';
import { supabaseServer } from '@/integrations/supabase/server';
import VenueFinderWrapper from '@/components/VenueFinderWrapper';

const SITE_URL = "https://www.showcase-music.com";

export async function generateMetadata({ 
  params 
}: { 
  params: Promise<{ 
    continentSlug: string;
  }> 
}): Promise<Metadata> {
  const { continentSlug } = await params;
  
  // Query venue_location_seo for this continent
  const { data: seoData } = await supabaseServer
    .from('venue_location_seo')
    .select('seo_title, meta_description, meta_keywords')
    .eq('continent', continentSlug)
    .is('country', null)
    .is('region', null)
    .is('city', null)
    .single();
  
  // Build title and description with fallbacks
  const continentName = continentSlug.replace(/-/g, ' ').replace(/\b\w/g, l => l.toUpperCase());
  const title = seoData?.seo_title || `Venues in ${continentName} | Showcase Music Directory`;
  const description = seoData?.meta_description || `Find concert venues across ${continentName}. Browse arenas, theatres, clubs and stadiums by country.`;
  
  return {
    title,
    description,
    keywords: seoData?.meta_keywords?.split(',').map(k => k.trim()),
    alternates: {
      canonical: `${SITE_URL}/venues/${continentSlug}`
    },
    openGraph: {
      title,
      description,
      url: `${SITE_URL}/venues/${continentSlug}`,
      siteName: 'Showcase Music Directory',
      type: 'website',
    },
    twitter: {
      card: 'summary',
      title,
      description,
    },
  };
}

export default function ContinentVenuesPage() {
  return <VenueFinderWrapper />;
}